#container{
  position: relative;
  /*Vertically Center*/
  min-height: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items:center;
          align-items:center;
}
#container:after, #loading:after {
  content:"";
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  /*Background Image*/
  background-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/d/d9/Rss-feed.svg/2000px-Rss-feed.svg.png');
  background-size:contain;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.3;
  z-index:-1;
  background-color: rgba(255, 165, 0, 0.3);
}

.fa-plus, .fa-times {
  cursor: pointer;
}

.fa-plus {
  color: green;
}

.fa-times {
  color: red;
}

.card {
  margin-top:0.5rem;margin-bottom:0.5rem;
}

#manageFeedSources, #viewFeeds {
  padding-top:1rem;
  padding-bottom:1rem;
}

#loading {
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center;
  position:fixed;
  z-index:999;
  height:100%;
  width:100%;
  margin:auto;
  top:0;
  left:0;
  bottom:0;
  right:0;
}

.lds-ripple {
  z-index:1000;
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #007bff;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}

h1, h5, .card-header, .card-header a,.card-title {
  font-family: 'Roboto Condensed',sans-serif;
  font-size:35px;
}

h3, h6, button, input, a, .list-group-item, .card-text, .card-link, .card-footer {
  font-family: 'Robot Condensed',sans-serif;
  font-size: 16px;
}

figure,img {
  width:100%;
}

figure,a,img {
  max-width: 100%;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



